
import { defineComponent, reactive, toRefs, computed } from 'vue'
interface dataProps {}
export default defineComponent({
  name: 'tbShowmore',
  props: {
    //   
    len: {
      type: Number,
      default: -1,
    },
    // 
    text: {
      type: String,
      default: '',
    },
    // 
    showText: {
      type: String,
      default: '',
    },
    // 
    hiddenText: {
      type: String,
      default: '',
    },
    // hiddenText  
    allowFold: Boolean,
  },
  setup(props, context) {
    const data = reactive({
      tbTextLen: props.len,
    })

    const refData = toRefs(data)
    // 
    const filterText = computed(() => {
      if (refData.tbTextLen.value !== -1 && refData.tbTextLen.value < props.text.length) {
        return props.text.substring(0, refData.tbTextLen.value) + '...'
      }
      return props.text
    })
    const showMore = () => {
      refData.tbTextLen.value = refData.tbTextLen.value === props.text.length ? props.len : props.text.length
    }
    return { ...refData, filterText, showMore }
  },
})
